html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.flex {
  display: flex;
}

.f-aligncenter {
  display: flex;
  align-items: center;
}

.d-spacebeetwen {
  display: flex;
  justify-content: space-between;
}

.j-center {
  display: flex;
  justify-content: center;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/Quicksand-Regular.woff2") format("woff2"), url("../fonts/Quicksand-Regular.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 400;
  display: swap;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/Quicksand-Medium.woff2") format("woff2"), url("../fonts/Quicksand-Medium.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 500;
  display: swap;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/Quicksand-Bold.woff2") format("woff2"), url("../fonts/Quicksand-Bold.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 700;
  display: swap;
}

@font-face {
  font-family: 'icomoon-Moderno';
  src: url("../fonts/icomoon-Moderno.eot?exr80l");
  src: url("../fonts/icomoon-Moderno.eot?exr80l#iefix") format("embedded-opentype"), url("../fonts/icomoon-Moderno.ttf?exr80l") format("truetype"), url("../fonts/icomoon-Moderno.woff?exr80l") format("woff"), url("../fonts/icomoon-Moderno.svg?exr80l#icomoon-Moderno") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon-Moderno' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

body {
  color: #263238;
  font-family: "Quicksand";
  font-weight: 400;
  font-size: 16px;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.container--fluid {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

.header__top {
  padding: 25px 0;
  background-color: #eef1f3;
}

.saller--none {
  display: none;
}

.user {
  display: none;
}

.header__top-saller,
.user__signIn,
.user__signUp {
  padding: 10px 12px;
  color: #fff;
  background-color: #0674ec;
  border-radius: 27px;
  margin-left: 10px;
}

.header__top-saller:hover,
.header__top-saller:active,
.user__signIn:hover,
.user__signUp:hover,
.user__signIn:active,
.user__signUp:active {
  color: #0674ec;
  background-color: #fff;
  border: 1px solid #0674ec;
}

.account__cart-icon,
.account__notice-icon,
.account__massage-icon {
  width: 20px;
  height: 20px;
  fill: #7e8fa1;
}

.account__cart-icon:hover,
.account__notice-icon:hover,
.account__massage-icon:hover {
  fill: #000;
  width: 22px;
  height: 22px;
}

.account__massage-counter,
.account__cart-counter,
.account__notice-counter {
  border-radius: 50%;
  padding: 4px 5px;
  color: #fff;
  font-family: 'Quicksand';
  font-weight: 500;
  font-size: 10px;
  position: relative;
  bottom: 16px;
  right: 14px;
}

.account__notice-counter {
  background-color: #ff6a6c;
}

.account__cart-counter {
  background-color: #0674ec;
}

.account__massage-counter {
  background-color: #62d03b;
}

.account__user-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}

.account__user-info {
  width: 100px;
  margin-left: 22px;
  line-height: 22px;
}

.account__user-info--name {
  font-family: 'Quicksand';
  font-weight: 500;
  font-size: 16px;
  color: #333;
}

.account__user-info--name:hover {
  border-bottom: 1px solid #000;
}

.account__user-info--cash {
  color: #0674ec;
  margin-top: 5px;
}

.account__user-info--cash:hover {
  border-bottom: 1px solid #0674ec;
}

.navigation__form-btn {
  width: 70px;
  height: 46px;
  background-color: #0674ec;
  border-radius: 23px;
  border: none;
  position: relative;
  top: 3px;
  right: 70px;
  cursor: pointer;
}

.navigation__form-btn:hover {
  background-color: #0a325e;
}

.navigation__form-btn--icon {
  width: 20px;
  height: 20px;
  fill: #fff;
}

.navigation {
  padding: 15px 0;
}

.menu__list-item {
  margin-left: 10px;
}

.menu__list-item:first-child {
  margin-left: 0;
}

.menu__list-link {
  text-transform: uppercase;
  font-family: 'Quicksand';
  font-weight: 700;
  color: #333;
  font-size: 15px;
}

.menu__list-link:hover {
  background-color: #0674ec;
  color: #fff;
  padding: 10px;
  border-radius: 5%;
}

.navigation__form-searche {
  width: 360px;
  height: 46px;
  border-radius: 23px;
  border: solid 1px #d9dfe5;
  padding: 0 25px;
}

.martplace {
  background-image: linear-gradient(to top, #0030de, #2e5afb);
  color: #fff;
  padding: 185px 0 235px;
}

.martplace__content {
  margin: 0 auto;
}

.martplace__title {
  display: block;
  font-family: 'Quicksand';
  font-weight: 700;
  font-size: 3.75rem;
  width: 50.1875rem;
}

.martplace__title--regular {
  font-weight: 400;
  font-size: 60px;
}

.martplace__content-text {
  font-weight: 500;
  font-size: 20px;
}

.martplace__content-link {
  margin-top: 68px;
}

.martplace__content-link--all,
.martplace__content-link--popular {
  color: #fff;
  padding: 20px 32px;
  border: solid 1px #ffffff;
  border-radius: 27px;
  margin-left: 30px;
}

.martplace__content-link--all:hover,
.martplace__content-link--popular:hover {
  color: #0674ec;
  background-color: #fff;
}

.martplace__content-link--all:active,
.martplace__content-link--popular:active {
  font-size: 17px;
}

@media (max-width: 720px) {
  .container--fluid {
    padding: 0 15px;
  }
}
